.advantages {
    margin: 30px auto;
    &__title {
        margin-bottom: 30px; }
    &__items {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        margin: -12px; } }

.item {
    flex: 0 0 20%;
    width: 20%;
    text-align: center;
    padding: 16px 12px 0;
    @include sm {
        min-width: 144px;
        padding: 24px 10px 0; }
    &__image {
        margin-bottom: 16px;
        .icon {
            font-size: 60px;
            display: inline-block; } }
    &__text {
        font-weight: 700;
        font-style: italic; } }

.clients {
    &__logos {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap; }
    &__logo {
        @include icenter;
        filter: grayscale(100%) opacity(0.7);
        transition: filter $tr;
        &:hover {
            filter: grayscale(0) opacity(1); }
        .slick-active & {
            filter: grayscale(0) opacity(1); }
        img {
            max-width: 100%; } } }

.slick-track {
    display: flex;
    align-items: center; }

.slick-slide div {
    @include vertical; }

.discount {
    height: 200px;
    @include cover;
    position: relative;
    text-align: center;
    &:after {
        @include coverdiv;
        content: '';
        background-color: rgba(#f8fbf1, 0.92);
        z-index: 1; }
    &__title {
        font-size: 20px;
        line-height: 1.4;
        font-weight: 400;
        position: relative;
        z-index: 2; }
    .btn-discount {
        position: relative;
        z-index: 2; } }

.btn-discount {
    display: inline-block;
    text-align: center;
    line-height: 1.75;
    border: 2px solid $green;
    width: 390px;
    padding: 12px 10px;
    background-color: $white;
    font-size: 16px;
    font-family: $astra;
    font-weight: 700;
    word-spacing: 0.07em; }

.awards {
    &__items {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        margin: -12px; } }
