//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$astra: 'PTAstraSans';

@font-face {
	@include fontdefault(normal, 400, $astra);
	@include fontpath('PTAstraSans-Regular', 'PTAstraSans-Regular', PTAstraSans-Regular); }
@font-face {
	@include fontdefault(normal, 700, $astra);
	@include fontpath('PTAstraSans-Bold', 'PTAstraSans-Bold', PTAstraSans-Bold); }
@font-face {
	@include fontdefault(italic, 400, $astra);
	@include fontpath('PTAstraSans-Italic', 'PTAstraSans-Italic', PTAstraSans-Italic); }
@font-face {
	@include fontdefault(italic, 700, $astra);
	@include fontpath('PTAstraSans-BoldItalic', 'PTAstraSans-BoldItalic', PTAstraSans-BoldItalic); }
