.halls {
    &__halls {
        @include flex-center;
        flex-wrap: wrap;
        margin: 0 -15px; } }
.hall {
    flex: 0 0 50%;
    width: 50%;
    padding: 0 15px 30px;
    text-align: center;
    @include sm {
        flex: 0 0 100%;
        width: 100%; }
    &__image {
        display: block;
        @include cover;
        padding-bottom: 314px;
        margin-bottom: 28px; }
    &__title {
        font-size: 20px;
        font-weight: 700;
        line-height: 1.05;
        text-transform: uppercase;
        margin-bottom: 8px; }
    &__props {
        font-size: 18px;
        line-height: 1.17; }
    &__descr {
        max-width: 480px;
        margin: 0 auto;
        line-height: 2; }
    &__link {
        font-size: 16px;
        color: $darkGreen;
        border-bottom: 1px solid $darkGreen; } }
