/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

body {
  background: #FFF;
  line-height: 1.5;
  font-family: "PTAstraSans", sans-serif;
  font-size: 14px;
}

a {
  color: inherit;
  text-decoration: none;
}

.title {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 700;
  text-align: center;
  line-height: 1.31;
}

.title span {
  display: inline-block;
  padding: 12px;
  border-bottom: 2px solid #8ade6d;
}

section {
  margin-bottom: 24px;
}

.container-inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

@media only screen and (max-width: 1023px) {
  .container-inner {
    max-width: 960px;
  }
}

@media only screen and (max-width: 767px) {
  .container-inner {
    max-width: 100%;
  }
}

@media only screen and (max-width: 1023px) {
  .tablet-hide {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .mob-block {
    display: block;
  }
}

.block {
  display: flex;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  padding-top: 50px;
}

@media only screen and (max-width: 1023px) {
  .header {
    padding-top: 36px;
  }
}

@media only screen and (max-width: 767px) {
  .header {
    padding-top: 8px;
  }
}

.header__container {
  display: flex;
  align-items: flex-start;
}

@media only screen and (max-width: 767px) {
  .header__container {
    align-items: center;
  }
}

.header__logo {
  margin-right: auto;
}

.nav {
  position: relative;
}

@media only screen and (max-width: 767px) {
  .nav {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    padding-top: 63px;
    text-transform: uppercase;
    background-color: rgba(0, 0, 0, 0.96);
    transition: opacity 0.7s, visibility 0.7s;
    z-index: -1;
  }
}

@media only screen and (max-width: 767px) {
  .nav.is-open {
    visibility: visible;
    opacity: 1;
    transition: opacity 0.7s, visibility 0.7s;
  }
}

.nav__list {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

@media only screen and (max-width: 767px) {
  .nav__list {
    flex-flow: column;
    padding: 16px 0;
  }
}

.nav__item {
  color: #fff;
}

.nav__item:not(:first-child) {
  margin-left: 42px;
}

.nav__item:last-child {
  text-align: right;
}

@media only screen and (max-width: 767px) {
  .nav__item:not(:first-child), .nav__item:first-child {
    margin: 16px auto;
  }
}

.nav__link {
  color: #fff;
}

.nav__phone {
  font-size: 24px;
  display: block;
  line-height: 1;
}

.nav__email {
  display: block;
  margin-top: 4px;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 300;
}

.logo {
  display: block;
}

.logo img {
  display: inline-block;
  vertical-align: middle;
  margin-right: 30px;
}

@media only screen and (max-width: 767px) {
  .logo img {
    margin-right: 12px;
  }
  .logo img img {
    height: 48px;
  }
}

.toggle-button {
  position: relative;
  z-index: 20;
  width: 32px;
  height: 24px;
  display: none;
}

@media only screen and (max-width: 767px) {
  .toggle-button {
    display: block;
  }
}

.toggle-button span {
  display: block;
  width: 100%;
  height: 2px;
  width: 24px;
  background: white;
  margin: 4px auto;
  transition: all 0.3s;
  backface-visibility: hidden;
}

.toggle-button.on span:nth-child(1) {
  transform: rotate(45deg) translate(2px, 2px);
}

.toggle-button.on span:nth-child(2) {
  opacity: 0;
}

.toggle-button.on span:nth-child(3) {
  transform: rotate(-45deg) translate(6px, -7px);
}

.lang {
  display: flex;
  justify-content: flex-end;
}

.lang__item {
  position: relative;
}

.lang__item:not(:last-child) {
  margin-right: 5px;
}

.lang__item:not(:last-child):after {
  content: '/';
  margin-left: 5px;
}

.lang__link {
  border-bottom: 1px solid #fff;
}

.hero {
  height: 100vh;
  min-height: 630px;
  background-color: rgba(43, 46, 38, 0.5);
}

.hero .error {
  color: #fff;
}

.hero__video {
  position: relative;
  height: 100%;
  width: 100%;
}

.hero__video:before {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  content: "";
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.2);
}

.hero__container {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 4;
  text-align: center;
  font-size: 0;
}

.hero__container:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.hero__text {
  display: inline-block;
  vertical-align: middle;
  color: #ffffff;
  text-align: center;
  max-width: 900px;
  min-width: 830px;
  padding: 150px 15px 150px;
  overflow: hidden;
}

@media only screen and (max-width: 1023px) {
  .hero__text {
    max-width: 800px;
    min-width: initial;
    padding: 150px 15px 150px;
  }
}

@media only screen and (max-width: 767px) {
  .hero__text {
    max-width: 640px;
  }
}

.hero__title {
  margin-bottom: 14px;
  font-size: 24px;
  line-height: 2.83;
  text-transform: uppercase;
  font-weight: 400;
}

@media only screen and (max-width: 1023px) {
  .hero__title {
    font-size: 28px;
  }
}

@media only screen and (max-width: 767px) {
  .hero__title {
    font-size: 24px;
  }
}

.hero__name {
  font-size: 29px;
  line-height: 1.45;
  color: #8ade6d;
  text-transform: uppercase;
}

.hero__form {
  margin: 0 auto;
}

.calculate__field {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0 -15px 32px;
  text-align: center;
  position: relative;
}

@media only screen and (max-width: 767px) {
  .calculate__field {
    flex-flow: column;
    margin: 20px 0;
    padding: 0 15px;
  }
}

.calculate__input-group {
  flex-grow: 1;
  position: relative;
  margin: 0 15px;
  position: relative;
  border: 3px solid #ccc;
  background-color: #fff;
}

@media only screen and (max-width: 767px) {
  .calculate__input-group {
    margin-bottom: 16px;
  }
}

.calculate__icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 12px;
  font-size: 0;
}

.calculate__icon .icon {
  font-size: 14px;
  color: #ccc;
  display: inline-block;
  vertical-align: middle;
}

.calculate__input {
  display: block;
  width: 100%;
  background-color: transparent !important;
  font-size: 14px;
  font-weight: 400;
  font-style: italic;
  padding-left: 34px;
  font-family: "PTAstraSans", sans-serif;
  height: 43px;
}

.calculate__input::placeholder {
  background-color: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  color: inherit;
}

.calculate__input:focus {
  border-bottom-color: rgba(255, 255, 255, 0.7);
}

.calculate__input:focus::placeholder {
  font-size: 0;
}

.calculate__input.error {
  color: rgba(255, 255, 255, 0.7);
}

@media only screen and (max-width: 767px) {
  .calculate__input {
    border-bottom-width: 1px;
  }
}

.calculate__submit {
  display: inline-block;
  height: 45px;
  line-height: 45px;
  font-size: 16px;
  min-width: 306px;
  text-align: center;
  color: #000;
  line-height: 1.83;
  text-align: center;
  background-color: #8ade6d;
  text-transform: uppercase;
  border: 1px solid #2c6817;
}

.calculate__submit:disabled {
